<template>
  <v-dialog
    width = "500"
    v-model = "profileDialog"
  >
    <v-card
      color = "primary"
      dark class = "rounded-b-0"
    >
      <v-card-title
        color = "white"
        class = "mx-auto text-h5 justify-center"
        style = "word-break: break-word;"
      >LOGIN & REGISTRATION</v-card-title>
    </v-card>

    <v-card class = "rounded-t-0">
      <v-tabs fixed-tabs color = "secondary">
        <v-tab>Login</v-tab>
        <v-tab>Register</v-tab>

          <!-- login -->
          <v-tab-item class = "pa-5">
            <v-text-field
              filled
              dense
              rounded
              label = "Email or username..."
            ></v-text-field>
            <v-text-field
              filled
              hide-details
              dense
              rounded
              label = "Password..."
              type="password"
            ></v-text-field>
            <v-checkbox label = "I am not a robot..."></v-checkbox>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color = "error">Cancel</v-btn>
              <v-btn color = "success">Login</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-tab-item>

          <!-- register -->
          <v-tab-item  class = "pa-5">
            <v-text-field
              filled
              dense
              rounded
              label = "Username..."
            ></v-text-field>
            <v-text-field
              filled 
              dense
              rounded
              label = "Email..."
            ></v-text-field>
            <v-text-field
              filled
              dense
              rounded
              label = "Password..."
              type="password"
            ></v-text-field>
            <v-text-field
              filled
              dense
              rounded
              hide-details
              label = "Confirm password..."
              type="password"
            ></v-text-field>
            <v-checkbox label = "I am not a robot..."></v-checkbox>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color = "error">Cancel</v-btn>
              <v-btn color = "success">Register</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-tab-item>
      </v-tabs>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  data () {
    return {
     profileDialog: false, 
    }
  },
  methods: {
    openProfileDialog() {
      this.profileDialog = true;
    },
  },
}
</script>