<template>
    <v-navigation-drawer
      v-model = "drawer"
      bottom
      temporary
      absolute
      color = "primary"
      app
      mobile-breakpoint = "0"
    >

      <!-- menu for site navigation -->
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title class = "text-h6">
            WEBSITE NAVIGATION
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list
        dense
        nav
      >
        <v-list-item
          v-for = "item in menuItems"
          :key = "item.title"
          :to = "item.location"
          link
        >
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>

      <v-divider></v-divider>

      <!-- menu buttons for misc things -->
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title class = "text-h6">
            MISC MENUS
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list
        dense
        nav
      >
        <v-list-item
          link
          @click = "$refs.menuDrawer.openDialog()"
        >
          <v-list-item-icon>
            <v-icon>mdi-cog</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>Settings</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    <GlobalSettings ref = "menuDrawer"></GlobalSettings>
    </v-navigation-drawer>
</template>

<script>
import GlobalSettings from "./GlobalSettings.vue"
export default {
  name: 'Sidebar',
    components: {
      GlobalSettings,
    },
  data () {
    return {
      drawer: false,
      menuItems: [
        { title: 'Home', icon: 'mdi-home', location: '/'},
        { title: 'About', icon: 'mdi-information', location: '/about' },
        { title: 'Contact', icon: 'mdi-email', location: '/contact' },
        { title: 'Timeline', icon: 'mdi-chart-gantt', location: '/timeline' },
      ],
    }
  },
  methods: {
    openDrawer() {
      this.drawer = true;
    }
  }
}
</script>