          </v-card-title>
          <v-card-text>
            {{ item.text }}
          </v-card-text>
        </v-card>
      </v-timeline-item>
    </v-timeline>

    <!-- for smaller screens -->
    <v-timeline v-else dense>
      <v-timeline-item
        v-for = "item in timelineItems"
        :key = "item.span"
        color = "primary"
        :icon = "item.icon"
