          { span: '09/04/2022', icon: "mdi-navigation-variant-outline", title: 'Navbar And Drawer', text: `Gotten comfortable with how some of the components for vue.js work.
                                                                                                           It's so easy to understand what each thing does and how they work together.
                                                                                                           And now the first iteration of the navigation bar is finished!
