                                                                                                   It requires input in every field, as well as matching passwords when registering!
                                                                                                   The buttons to log in and register are not functional as of yet.
                                                                                                   Now it is time to learn mongoDB, and set up a secure database for account creation and storage.`, },

          { span: '01/06/2022', icon: "mdi-transfer", title: 'Rework And Migration', text: `A massive rework of the website, and migration to fullstack is under way.
