                                                                                            A lot of work still has to be done, but we are slowly getting there.
                                                                                            Some features that was on the previous side (as mentioned above) might not work anymore.
                                                                                            But that will eventualy be fixed!`, },

          { span: '13/06/2022', icon: "mdi-cog", title: 'Settings', text: `The settings dialog in the side menu is now back!
                                                                           At the moment the only setting added is "Darkmode".
                                                                           This makes the website night time friendly.
                                                                           More settings will come later. Suggestions
                                                                           for what settings to add can be requested on the github page`, },
        ],
