<template>
    <v-dialog
      v-model = "drawer"
      width = "500"
    >
      <v-card
        color = "primary"
        dark
      >
        <v-card-title
          class = "mx-auto text-h5 justify-center"
          style = "word-break: break-word;"
        >
        SETTINGS</v-card-title>
      </v-card>
      <v-card>
        <v-switch
          class = "mt-0 pt-5 pl-5"
          v-model = "darkmodeSwitch"
          @click = "toggleDarkmode()"
          color = "secondary"
          label = "Toggle Darkmode"
        ></v-switch>
      </v-card>
    </v-dialog>
</template>

<script>
export default {
  data () {
    return {
      drawer : false,
      darkmodeSwitch: false,
    }
  },
  mounted() {
    this.checkIfDarkmodeActivated()
  },
  methods: {
    openDialog() {
      this.drawer = true;
    },
    toggleDarkmode() {
      this.$vuetify.theme.dark=!this.$vuetify.theme.dark;
      localStorage.setItem("useDarkMode", this.$vuetify.theme.dark.toString())
    },
    checkIfDarkmodeActivated(){
      const isDarkmode = localStorage.getItem("useDarkMode");
      if (isDarkmode) {
        if (isDarkmode == "true") {
          this.$vuetify.theme.dark = true;
          this.darkmodeSwitch = true;
        } else {
          this.$vuetify.theme.dark = false;
          this.darkmodeSwitch = false;
        }
      }
    },
  }
}
</script>