<template>
  <v-card
    max-width = "800"
    class = "mx-auto my-12 px-5 py-5"
    elevation = "4"
  >
    <v-row>
      <v-col cols = "12">
        <form
          lazy-validation
          ref = "form" @submit.prevent="sendEmail"
        >
        <v-row>
          <v-col
            cols = "12"
            sm = "6"
          >
            <v-text-field
              v-model = "name"
              :rules = "nameRules"
              label = "Name"
              name = "user_name"
              required        
              color = "info"
            ></v-text-field>
          </v-col>

          <v-col
            cols = "12"
            sm = "6"
          >
            <v-text-field
              v-model = "email"
              :rules = "emailRules"
              label = "E-mail"
              required
              color = "info"
              name = "user_email"
            >
            </v-text-field>
          </v-col>
        </v-row>

          <v-select
            v-model = "select"
            :items = "items"
            :rules = "[v => !!v || 'Please select an option']"
            required
            label = "What can i help you with?"
            color = "info"
            item-color="info"
            name = "user_issue"
          ></v-select>

          <v-row>
            <v-col cols="12">
            <v-textarea
              color = "info"
              required
              :rules = "msgRules"
              name = "user_msg"
            >
              <template v-slot:label>
                <div>
                  Message
                </div>
              </template>
            </v-textarea>
          </v-col>
          </v-row>

          <v-checkbox
            v-model = "checkbox"
            :rules = "[v => !!v || 'Wait, are you a robot?']"
            label = "I am not a robot!"
            required
            color = "info"
          ></v-checkbox>

          <button type="submit" value="Send" class="mr-4 v-btn v-btn--is-elevated v-btn--has-bg theme--light v-size--default success"><span class="v-btn__content">Send</span></button>

          <v-btn
          color = "error"
          class = "mr-4"
          @click = "reset"
          >Reset</v-btn>
        </form>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
  import emailjs from '@emailjs/browser';

  export default {
    name: "Contact",
    data() {
      return {

      }
    },

    data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 30) || 'Name must be less than 30 characters',
        v => (v && v.length >= 3) || 'Name must be 3 characters or longer',
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
      ],
      select: null,
      items: [
        'Technical issue',
        'Website issue',
        'Account issue',
        'Product issue',
      ],
      msgRules : [
        v => !!v || 'A message is required',
      ],
      checkbox: false,
    }),
    methods: {
      validate () {
        this.$refs.form.validate()
      },
      reset () {
        this.$refs.form.reset()
      },
      sendEmail() {
        emailjs.sendForm(process.env.VUE_APP_SERVICE, process.env.VUE_APP_TEMPLATE_KEY, this.$refs.form, process.env.VUE_APP_API_KEY)
        .then((result) => {
            console.log('SUCCESS!', result.text);
            this.$router.push('/contactsent')
        }, (error) => {
            console.log('FAILED...', error.text);
        });
      },
    },
  }
</script>